footer {
   background-color: $color-background;
   color: #fff;
   padding: 30px 0 0;
   position: relative;

   .footer-row {
      display: flex;
      flex-wrap: wrap;

      .footer-menu {
         flex: 100%;
         margin-bottom: 30px;
         
         @media (min-width: $breakpoint-small) {
            flex: 50%;
         }

         @media (min-width: $breakpoint-medium) {
            flex: 25%;
         }

         @media (min-width: $breakpoint-large) {
            margin-bottom: 40px;
         }

         h4 {
            text-transform: uppercase;
            font-size: 1rem;
            margin-bottom: 26px;
            margin-top: 0;
            padding-top: 0;
         }

         ul {
            margin: 0;
            padding-left: 0;
         }
         
         li {
            list-style-type: none;
            padding: 0.1em;
         }

         a {
            color: #fff;
            text-decoration: none;

            &:hover {
               text-decoration: underline;
            }
         }
      }

      .footer-company {
         height: 200px;

         .footer-social-media {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 8px;
            margin-top: 0;
         }

         h4 {
            text-transform: uppercase;
            font-size: 1rem;
            margin-bottom: 26px;
            margin-top: 26px;
            padding-top: 0;
         }

         a {
            color: #fff;
            text-decoration: none;
            background-color: $color-primary-dark;
            display: block;
            width: 64px;
            height: 56px;
         }
      }
   }
}